/* Scoped styles for the aaeaddon widget */
.aaeaddon-post-reactions {
  display: flex;
  flex-wrap: wrap;
  gap: 10px;
  justify-content: center;
}

.aae-reaction-count {
  display: inline-block;
  padding-left: 7px;
}

.aaeaddon-reaction-btn {
  margin: 5px;
  padding: 10px 20px;
  font-size: 16px;
  cursor: pointer;
  background-color: #f1f1f1;
  border: 1px solid #ccc;
  border-radius: 5px;
  transition: background-color 0.3s;
}

.aaeaddon-reaction-btn:hover {
  background-color: #e1e1e1;
}

.aaeaddon-reaction-btn i.aaeaddon-reaction-icon {
  font-size: 20px; /* Adjust for font icon size */
  margin-right: 5px;
}

.aaeaddon-reaction-btn img.aaeaddon-reaction-icon {
  width: 20px; /* Adjust size as needed */
  height: 20px;
  vertical-align: middle;
  margin-right: 5px;
}